/* Pilot PJ : 서브페이지 공통 CSS - fashion.css */
html,
body {
  overflow: visible;
}
body {
  overflow-x: hidden;
}

/* 배너박스 */
#ban {
  height: 100vh;
}

/* 스와이퍼 디자인설정 */
.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.swiper {
  margin-left: auto;
  margin-right: auto;
}

/* 1. 신상품 컨텐츠 박스 */
