/* 움직일 리스트박스 */
.flist {
  position: relative;
  left: 0; /* 움직일 기준값 */
  display: flex;
}
/* 리스트 하위 li */
.flist li {
  position: relative;
  /* 부모자격 - 설명박스 */
}
/* 리스트 상품 이미지 */
.flist li img {
  width: 300px;
}

/* 신상정보박스 */
.ibox {
  position: absolute;
  /* 부모는? 싸고있는 li */
  top: 60%;
  /* 105%로 내려올 것임 */
  width: 100%;
  text-align: center;
  font: 14px/24px UB;
  background-color: #101629;
  color: #fff;
  padding: 20px 0;
  opacity: 0;
  /* 1로 투명도 변경될 것임 */
  /* 처음에 투명 */
}

/* 가상요소로 안쪽 테두리 */
.ibox::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 2px solid #fff;
  transform: scaleX(0.94) scaleY(0.85);
  box-sizing: border-box;
  /* 보더때문에 늘어나지 않게 처리! */
}

/* 가상요소로 화살표만들기 */
.ibox::after {
  content: "";
  display: block;
  position: absolute;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
  /* 삼각형설정 */
  width: 0;
  height: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-bottom: 20px solid #101629;
  /* 삼각형 */
  /* border-top: 20px solid #101629;역삼각형 */
}

