/*신상품 상세정보 영역*/
.gdesc {
  padding: 15px;
  box-sizing: border-box;
  overflow: auto;
}
.gdesc ol {
  margin: 0;
  padding: 0;
  list-style: none;
  width: 80%;
  margin-left: 20px;
}
.gdesc ol > li {
  width: 100%;
  font-family: "Nanum Gothic";
  font-size: 12px;
  line-height: 3.5vh;
}
.gdesc ol > li > span:first-child {
  /*각항목 타이틀*/
  display: inline-block;
  width: 70px;
  font-size: 14px;
}
.gdesc ol > li:nth-child(3) {
  border-bottom: 1px solid #ccc;
}

/*이미지 정렬 공통*/
.gdesc ol > li img {
  vertical-align: middle; /*인라인 태그 정렬 세로중앙*/
  margin: 0 4px;
}

/*상품명*/
